
import { defineComponent } from 'vue'
export default defineComponent({
    name: "SelectLocale",
    data() {
        return {
            locales: [
                { name: 'Portugus', locale: 'pt' },
                { name: 'English', locale: 'en' },
            ],
        }
    },
    methods: {
        changeLocale(locale: string){
            this.$i18n.locale = locale;
            localStorage.setItem('locale', locale);
        }
    },
})
